import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import type { PersistPartial } from "redux-persist/es/persistReducer";

export interface User { /* ... */ }

export interface AuthState {
  user: User | null;
  access: string | null;
  refresh: string | null;
  isAuthenticated: boolean;
  isLoading: boolean;
}

const initialState: AuthState = {
  user: null,
  access: null,
  refresh: null,
  isAuthenticated: false,
  isLoading: false,
};

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    setCredentials: (state, action: PayloadAction<{ user: User; access?: string; refresh?: string }>) => {
      state.user = action.payload.user;
      state.access = action.payload.access ?? null;
      state.refresh = action.payload.refresh ?? null;
      state.isAuthenticated = !!action.payload.access;
    },
    logout: (state) => {
      state.user = null;
      state.access = null;
      state.refresh = null;
      state.isAuthenticated = false;
    },
  },
});

export const { setCredentials, logout } = authSlice.actions;

// **Export reducer directly**
export default authSlice.reducer;
